.portfolio {
  padding: 100px 0;
  h2 {
    margin-bottom: 55px; }
  &__block {
    border-radius: 8px;
    background-color: $white;
    box-shadow: 0 4px 32px 0 rgba(0, 0, 0, 0.12);
    overflow: hidden;
    display: flex;
    flex-wrap: wrap;
    height: 290px;
    &__image {
      width: 47%;
      height: 290px;
      overflow: hidden;
      img {
        height: 100%; } }
    &__content {
      width: 53%;
      position: relative;
      &__text {
        padding: 25px 25px 0px; }
      h3 {
        font-size: 20px;
        line-height: 1.4;
        font-family: 'Museo Sans';
        margin-bottom: 15px;
        span {
          color: $primary; } }
      a {
        position: absolute;
        text-align: right;
        bottom: 0;
        width: 100%;
        text-decoration: none;
        color: $secondary;
        font-size: 14px;
        font-family: 'Museo Sans';
        letter-spacing: 0.4px;
        text-transform: uppercase;
        font-weight: 600;
        box-shadow: inset 0 1px 0 0 #dee5e9;
        display: flex;
        align-items: center;
        justify-content: flex-end;
        padding: 15px 25px;
        i {
          color: $primary;
          font-size: 32px;
          margin-right: 10px;
          @include tr; }
        &:hover {
          i {
            transform: scale(1.1); } } } } } }


