.partners {
  padding: 100px 0;
  h2 {
    margin-bottom: 55px; }
  &__row {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    div {
      width: 25%;
      display: flex;
      justify-content: center;
      margin-bottom: 40px;
      img {
        height: 50px;
        filter: grayscale(1);
        cursor: pointer;
        opacity: 0.4;
        @include tr;
        &:hover {
          filter: grayscale(0);
          transform: scale(1.1);
          opacity: 1; } } } } }
