.header {
  position: absolute;
  width: 100%;
  &__top {
    background: $secondary;
    height: 60px;
    margin-bottom: 15px; }
  &__lang {
    position: absolute;
    display: flex;
    height: 60px;
    background-color: rgba(255, 255, 255, 0.04);
    @include center;
    right: 0;
    top: 0;
    width: 110px;
    cursor: pointer;
    img {
      width: 22px;
      height: 18px; }
    p {
      color: $white;
      margin: 0 8px 0 10px; }
    i {
      color: rgba($white, 0.6);
      font-size: 7px; } }
  nav {
    ul {
      display: flex;
      justify-content: space-between;
      a {
        color: $white;
        opacity: 0.7;
        text-decoration: none;
        position: relative;
        display: inline-block;
        font-size: 16px;
        @include tr;
        &:before {
          content: '';
          border-top: 3px solid $primary;
          width: 100%;
          height: 3px;
          display: block;
          position: absolute;
          top: -21px;
          transform: translateX(-100%);
          opacity: 0;
          @include tr; }
        &:hover {
          opacity: 1;
          &:before {
            transform: translateX(0%);
            opacity: 1; } } } } }
  &__contact {
    float: right;
    text-align: right;
    p {
      font-size: 14px;
      font-weight: 500;
      color: rgba($white, 0.7);
      margin-bottom: 5px; }
    a {
      color: $white;
      text-decoration: none;
      @include center;
      img {
        padding-right: 10px; }
      i {
        color: $primary;
        @include s('mail-2'); } } } }




